import React from "react";
import {
    Button,
    Col
  } from "reactstrap";
import Header from "./Header";
import { Redirect } from 'react-router-dom';
import modelo_gameficacao from '../../assets/img/modelo_gameficacao.png';

class ProjectDescription extends React.Component{

    constructor(props)
    {
        super(props);
        this.state = {
            name:'',
            gender:'',
            educationalLevel:'',
            age:'',
            studyDomain:'',
            redirect: false
        }

        this.redirect = this.redirect.bind(this);
    }
    redirect(){
        let user_info = JSON.parse(localStorage.getItem('user'));
        let name = user_info.name;
        let gender = user_info.gender;
        let age = user_info.age;
        let educationalLevel = user_info.educationalLevel;
        let studyDomain = user_info.studyDomain;
        this.setState({name: name, gender: gender, age: age, educationalLevel: educationalLevel, studyDomain: studyDomain});
        this.setState({redirect: true});
        
    }

    render(){
        return(
            <div>
            <Header/>
            <Col sm="50" md={{ size: 8, offset: 2 }} className="text-justify">
            <p className= " text-center font-weight-bold " style={{fontFamily: 'Calibri', fontSize: '30px'}}>Modelo de Monitoramento e Adaptao do Design de Gamificao para Professores</p>
            <br/>
            <p style={{fontFamily: 'Calibri', fontSize: '20px'}}> 
            {/* Existe um interesse crescente em aplicar a gamificao em ambientes educacionais com o objetivo de aumentar a motivao e o engajamento dos alunos. No entanto, apesar dos benefcios do uso da gamificao, inclusive no contexto educacional, alguns estudos publicados na literatura relataram resultados no esperados em relao ao engajamento e motivao dos alunos aps a implementao da gamificao em ambientes educacionais.            
            <br/><br/> Uma possvel soluo para evitar esses resultados inesperados  monitorar a interao dos alunos e os efeitos da gamificao durante o processo de aprendizagem, e quando situaes crticas so detectadas, a adaptao  gamificao pode ser realizada para motivar os alunos a interagir mais com os recursos de aprendizagem do sistema e, consequentemente, melhorar seus resultados de engajamento e motivao. */}
            <br/>O Modelo de Monitoramento e Adaptao do Design de Gamificao para Professores'' foi desenvolvido com o objetivo de aumentar a chance de obter resultados positivos em relao ao engajamento e motivao dos alunos durante o processo de aprendizagem em sistemas educacionais gamificados. 
            <br/><br/><strong>Descrevendo o modelo: </strong>
            <li className = " text-justify ">
                &nbsp;<strong>Definio dos objetivos de interao:</strong> No referido modelo, os professores definem metas que esperam que os alunos atinjam em um determinado perodo de tempo. As metas de interao podem ser representadas por dois elementos para cada tpico do curso: nmero de interao esperados que os alunos tenham com os recursos e tempo esperado que essas interaes ocorram. Os recursos so os elementos disponveis no sistema educacacional que do suporte  aprendizagem dos alunos (por exemplo: vdeos, textos, questionrios, fruns). 
                Portanto, por exemplo, um objetivo de interao configurado por um professor poderia ser: esperar que os alunos interajam com pelo menos 70% dos recursos educacionais disponveis no ambiente de aprendizagem gamificado relacionados a um tpico em 2 semanas.
            </li>
            <li className = " text-justify ">
                &nbsp;<strong>Monitoramento da interao dos alunos com os recursos:</strong> Nesta etapa, os professores podem visualizar a interao dos alunos com os recursos disponveis no ambiente educacional gamificado e comparar se as interaes dos alunos esto como esperadas para atingir os objetivos de interao definidas pelo professor. 
                Os objetivos de interao definidos anteriormente podem servir como mtricas durante o processo de aprendizagem para os professores na fase de monitoramento, porque eles so capazes de avaliar se a classe/alunos est longe ou no de atingir os objetivos de interao definidos.
            </li>
            <li className = " text-justify ">
                &nbsp;<strong>Monitoramento da interao dos alunos com elementos de gamificao:</strong> Existem dois objetivos principais em mostrar a interao dos alunos com os elementos de gamificao para os professores. Primeiro, os professores podem visualizar as interaes dos alunos com os elementos do jogo implementados no sistema, aumentando a chance dos professores perceberem o impacto positivo da gamificao e, portanto, motivando-se para o uso da gamificao. 
                Segundo, no modelo, a adaptao do design de gamificao durante o processo de aprendizagem  realizada usando o elemento misso de gamificao, portanto,  necessrio que os professores possam visualizar quais misses so mais eficazes para motivar os alunos. Por meio dessas visualizaes, os professores podem ver quais misses foram mais bem-sucedidas e atribuir misses adequadamente ao longo do processo de aprendizado.
            </li>
            <li className = " text-justify ">
                &nbsp;<strong>Adaptao do design de gamificao por meio de misses:</strong> Como explicado anteriormente, a adaptao do design de gamificao no sistema educacional  feita por professores atravs da atribuio de misses, quando/se julgarem necessrio.
                Propomos o uso de misses para adaptar o design da gamificao durante o processo de aprendizagem, porque quando os professores percebem que as interaes dos alunos no esto como as esperadas, eles podem atribuir misses para motivar os alunos a interagir mais com os recursos educacionais disponveis no sistema e consequentemente atingir os objetivos de interao definidos. 
            </li>
            <br/><br/>
            A viso geral do modelo proposto pode ser visto na Figura 1.
            </p>

            <br/>
            <img src={modelo_gameficacao} alt="Modelo de Monitoramento e Adaptao do Design de Gameficao para Professores"/>
            <br/><br/><br/>
    
            <br/><br/>

            <center>
                <Button color ="#C0B283" type="button" onClick={this.redirect}> 
                    Prosseguir
                </Button>
                {this.state.redirect ? (<Redirect to={{ pathname:"/projecttutorial"}}/>) : (<div></div>)}
            </center>
            <br/>

            </Col>
            </div>
        );
    }

}

export default ProjectDescription;